.root {
    padding:5px;
    border-radius:50%;
    overflow:hidden;
    width: 32px;
    height:32px;
    background: rgba(255,255,255,0);
    transition: background .1s cubic-bezier(0.6, 0.04, 0.98, 0.335);
    border:none;
    &:hover{
        cursor: pointer;
        background: rgba(255,255,255,0.2)
    }
    * {
        width: 100%;
        height: 100%;   
    }
}