@import '../../variables.scss';
.root {
    height: 100vh;
    background: white;
    @include box-shadow(1);
    position: fixed;
    top: 0;
    left:0;
    width: 275px;
    z-index: 100;
    ul {
        list-style: none;
        height:100%;
        @include flexbox(column);
        justify-content: center;
        li {
            margin: 0.5rem 0;
        }
    }
}