@import '../../variables.scss';
.root {
    height: $header-height;
    position: sticky;
    left: 0;
    top: 0;
    font-size: 1.2rem;
    color: white;
    @include flexbox(row);
    padding: 0 20px;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.4));
    font-weight:500;
    .menuIcon {
        display: flex;
        @include respond-above(sm) {
            display:none;
        }
    }
    .brand {}
    .nav {
        @include flexbox(row);
        height:100%;
        overflow:auto;
        align-items: center;
        padding: 0 3em;
        text-transform: uppercase;
        flex-grow: 1;
        @include respond-below(sm) {
            display:none;
        }
        a {
            margin: 0 10px;
            color: rgba(255,255,555,0.5);
            border-bottom:  2px solid rgba(255,255,555,0);
        }
        .active {
            color: red;
            color: rgba(255,255,555,1);
            border-bottom:  2px solid rgba(255,255,555,1);
        }
    }
    .social {
        @include flexbox(row);
        .button {
            margin: 0 5px;
        }
    }
}